.cards{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 73%;
    height: 216px;
    margin-left:50px ;
    margin-top: 50px;
    border-radius: 16px;
    background: #0D0D0D;
    -webkit-box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
-moz-box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
}




