.sidebar {
    width: 180px;
    height: 560px;
    border-radius: 16px;
    background-color: #0d0d0d;
    color: white;
    text-align: center;
    margin-left: 45px;
    margin-top: 50px;
    -webkit-box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
    -moz-box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
    box-shadow: 0px 0px 8px 3px rgba(94, 165, 59, 0.25);
  }
  .sidebar h3{
    font-style: Poppins;
    font-weight: 600;
    font-size: 24px;
    line-height: 42px;
    padding-top: 15px;
  }
  .sidebar ul {
    justify-content: center;
    display: grid;
    gap: 20px;
    margin-top: 26px;
  }
  
  .sidebar a {
    padding-left: 13px;
    text-decoration: none;
    display: flex;
    align-items: center;
    gap: 10px;
    color: white;
    width: 162px;
    height: 54px;
    border-radius: 16px;
    transition: 1s ease;
  }
  .sidebar a:hover{
    color: #0d0d0d;
    background: #6A94FF;
  } 
  ul a i{
    font-size: 19px;
    padding-left: 5%;
  }
  .log{
    margin-top: 110px;
  }
